.auto-complete span {
  cursor: pointer;
}

.auto-complete input {
  display: none;
}
 .auto-complete ul{
   display: none;
 }

 .auto-complete li {
  color: blue;
  list-style: none;
}


svg path {
	stroke: #000;
	fill: transparent;
	stroke-width: 1px;
}



